import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimes } from '@fortawesome/free-solid-svg-icons';

class GifExportMenu extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      fps: 12,
      gifBackgroundColor: '#969696',
      gifSize: 4,
      gifName: 'piskel-clone',
    };

    this.fpsInput = React.createRef();
    this.sizeInput = React.createRef();
    this.colorInput = React.createRef();
    this.nameInput = React.createRef();

    this.changeFps = this.changeFps.bind(this);
    this.changeGifSize = this.changeGifSize.bind(this);
    this.changeGifBackgroundColor = this.changeGifBackgroundColor.bind(this);
    this.changeGifName = this.changeGifName.bind(this);
  }

  changeFps(newFPS) {
    this.setState({
      fps: newFPS,
    });
  }

  changeGifBackgroundColor(newBackgroundColor) {
    this.setState({
      gifBackgroundColor: newBackgroundColor,
    });
  }

  changeGifSize(newGifSize) {
    this.setState({
      gifSize: newGifSize,
    });
  }

  changeGifName(newGifName) {
    this.setState({
      gifName: newGifName,
    });
  }

  render() {
    const { exportGif, toggleExportMenu } = this.props;
    const {
      gifBackgroundColor,
      fps,
      gifSize,
      gifName,
    } = this.state;

    return (
      <div className="export-menu">
        <div className="export-field">
          Gif name:
          <input
            className="nameInput"
            type="text"
            value={gifName}
            onChange={() => this.changeGifName(this.nameInput.current.value)}
            ref={this.nameInput}
          />
        </div>

        <div className="export-field">
          Gif background color:
          <button
            className="color-button"
            style={{ backgroundColor: gifBackgroundColor }}
            onClick={() => this.colorInput.current.click()}
            type="button"
          >
            <input
              className="color-input"
              type="color"
              onChange={() => this.changeGifBackgroundColor(this.colorInput.current.value)}
              ref={this.colorInput}
              value={gifBackgroundColor}
            />
          </button>
        </div>

        <div className="export-field">
          FPS rate:
          {' '}
          {fps}

          <input
            type="range"
            className="fps-input"
            onChange={() => this.changeFps(this.fpsInput.current.value)}
            value={fps}
            min="0"
            max="24"
            ref={this.fpsInput}
          />
        </div>

        <div className="export-field">
          Gif size:
          {' '}
          {gifSize * 32}
          {' '}
          px

          <input
            type="range"
            className="size-input"
            onChange={() => this.changeGifSize(this.sizeInput.current.value)}
            value={gifSize}
            min="2"
            max="24"
            ref={this.sizeInput}
          />
        </div>

        <button
          onClick={() => {
            exportGif(gifBackgroundColor, fps, gifSize * 32, gifName);
            toggleExportMenu();
          }}
          type="button"
        >
          Export
        </button>

        <button
          className="export-exit-button"
          onClick={toggleExportMenu}
          type="button"
        >
          <FontAwesomeIcon icon={faTimes} />
        </button>
      </div>
    );
  }
}

GifExportMenu.propTypes = {
  exportGif: PropTypes.func.isRequired,
  toggleExportMenu: PropTypes.func.isRequired,
};

export default GifExportMenu;
