import React from 'react';
import PropTypes from 'prop-types';
import renderCanvas from '../utils/renderCanvas';

class PreviewArea extends React.Component {
  constructor(props) {
    super(props);

    this.animateFramesPreview = this.animateFramesPreview.bind(this);
    this.canvas = React.createRef();
  }

  componentDidUpdate() {
    clearTimeout(this.timer);
    this.animateFramesPreview();
  }

  animateFramesPreview() {
    const canvas = this.canvas.current;
    const { layers, fpsInputValue, pixelSize } = this.props;
    if (fpsInputValue === 0) return;
    let x = 0;

    function Frame() {
      if (x >= layers[0].length) x = 0;
      renderCanvas(canvas, layers[0][x], pixelSize / 2);

      for (let i = 1; i < layers.length; i += 1) {
        renderCanvas(canvas, layers[i][x], pixelSize / 2, false);
      }

      x += 1;
    }

    this.timer = setInterval(() => Frame(), 1000 / fpsInputValue);
  }

  render() {
    return (
      <canvas
        className="preview-canvas"
        width={384}
        height={384}
        ref={this.canvas}
      />
    );
  }
}

PreviewArea.propTypes = {
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
  fpsInputValue: PropTypes.number.isRequired,
};

export default PreviewArea;
