import React from 'react';
import PropTypes from 'prop-types';
import GIFEncoder from 'gif-encoder-2';
import GifExportMenu from './GifExportMenu';
import renderCanvas from '../utils/renderCanvas';
import createLink from '../utils/createLink';
import hexToRgb from '../utils/hexToRGB';

class ExportResultSetting extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isExportMenuActive: false,
    };

    this.exportGif = this.exportGif.bind(this);
    this.toggleExportMenu = this.toggleExportMenu.bind(this);
  }

  async exportGif(backGroundColor, fps, size, gifName) {
    const color = hexToRgb(backGroundColor);
    const { layers, pixelSize } = this.props;

    const canvas = document.createElement('canvas');
    canvas.width = size;
    canvas.height = size;
    const context = canvas.getContext('2d');
    const encoder = new GIFEncoder(size, size);
    const currPixelSize = (size / 768) * pixelSize;


    encoder.setDelay(1000 / fps);
    encoder.start();

    for (let i = 0; i < layers[0].length; i += 1) {
      context.fillStyle = `rgb(${color.join()})`;
      context.fillRect(0, 0, size, size);

      for (let j = 0; j < layers.length; j += 1) {
        renderCanvas(canvas, layers[j][i], currPixelSize, false);
      }

      encoder.addFrame(context);
      context.clearRect(0, 0, size, size);
    }

    encoder.finish();
    const link = createLink(encoder, gifName);
    link.click();
  }

  toggleExportMenu() {
    const { isExportMenuActive } = this.state;

    this.setState({
      isExportMenuActive: !isExportMenuActive,
    });
  }

  render() {
    const { isExportMenuActive } = this.state;

    return (
      <div className="settings-area">
        <button type="button" onClick={this.toggleExportMenu}>gif</button>
        <button type="button">.piskel</button>
        {isExportMenuActive
          && <GifExportMenu toggleExportMenu={this.toggleExportMenu} exportGif={this.exportGif} />
        }
      </div>
    );
  }
}

ExportResultSetting.propTypes = {
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
};

export default ExportResultSetting;
