import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import key from 'weak-key';
import Layer from './Layer';

function LayerSettings({
  layers,
  addLayer,
  deleteLayer,
  changeCurrentLayer,
  currentLayer,
  upLayer,
  downLayer,
}) {
  return (
    <div className="layer-settings">
      <div className="layer-settings-header">
        <button
          className="add-layer-button"
          onClick={addLayer}
          type="button"
        >
          <FontAwesomeIcon icon={faPlus} />
          Add layer
        </button>
      </div>

      <div className="layer-settings-container">
        {layers.map((layer, i) => (
          <Layer
            id={i}
            key={key(layer)}
            deleteLayer={deleteLayer}
            changeCurrentLayer={changeCurrentLayer}
            currentLayer={currentLayer}
            layers={layers}
            upLayer={upLayer}
            downLayer={downLayer}
          />
        ))}
      </div>
    </div>
  );
}

LayerSettings.propTypes = {
  deleteLayer: PropTypes.func.isRequired,
  changeCurrentLayer: PropTypes.func.isRequired,
  upLayer: PropTypes.func.isRequired,
  downLayer: PropTypes.func.isRequired,
  currentLayer: PropTypes.number.isRequired,
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  addLayer: PropTypes.func.isRequired,
};

export default LayerSettings;
