import React from 'react';
import PropTypes from 'prop-types';
import { faCog, faTimes } from '@fortawesome/free-solid-svg-icons';
import { faSave } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import key from 'weak-key';
import CanvasSizeSettings from './CanvasSizeSettings';
import ExportResultSetting from './ExportResultSetting';
import SettingsButton from './SettingsButton';


class Settings extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      atciveSettingButton: 0,
    };

    this.settings = React.createRef();
    this.openMenu = this.openMenu.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
  }

  openMenu(id, e) {
    e.stopPropagation();
    const settings = this.settings.current;
    settings.classList.add('activeMenu');

    this.setState({
      atciveSettingButton: id,
    });
  }

  closeMenu() {
    const settings = this.settings.current;
    settings.classList.remove('activeMenu');
  }

  render() {
    const icons = [faCog, faSave];
    const { atciveSettingButton } = this.state;
    const {
      changeCanvasSize,
      layers,
      pixelSize,
      canvasSize,
    } = this.props;

    return (
      <div
        className="settings"
        ref={this.settings}
      >
        <div className="buttons-container">
          {icons.map((item, i) => (
            <SettingsButton
              icon={item}
              atciveSettingButton={atciveSettingButton}
              id={i}
              key={key(item)}
              onClick={this.openMenu}
            />
          ))
          }
        </div>

        {atciveSettingButton === 0 && <CanvasSizeSettings changeCanvasSize={changeCanvasSize} />}
        {atciveSettingButton === 1
        && <ExportResultSetting layers={layers} pixelSize={pixelSize} canvasSize={canvasSize} />
        }

        <button
          className="exit-menu-button"
          onClick={this.closeMenu}
          type="button"
        >
          <FontAwesomeIcon icon={faTimes} />
        </button>
      </div>
    );
  }
}

Settings.propTypes = {
  changeCanvasSize: PropTypes.func.isRequired,
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
  canvasSize: PropTypes.number.isRequired,
};

export default Settings;
