import React from 'react';
import PropTypes from 'prop-types';
import key from 'weak-key';
import Frame from './Frame';
import AddFrameButton from './AddFrameButton';


function FramesControl({
  onClick,
  frames,
  currentFrame,
  copyFrame,
  deleteFrame,
  addFrame,
  pixelSize,
}) {
  return (
    <div className="frames-controle">
      {frames.map((frame, i) => (
        <Frame
          onClick={onClick}
          frames={frames}
          key={key(frame)}
          id={i}
          active={currentFrame === i}
          copyFrame={copyFrame}
          deleteFrame={deleteFrame}
          pixelSize={pixelSize}
        />
      ))
      }

      <AddFrameButton
        onClick={addFrame}
      />
    </div>
  );
}

FramesControl.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.array).isRequired,
  onClick: PropTypes.func.isRequired,
  currentFrame: PropTypes.number.isRequired,
  copyFrame: PropTypes.func.isRequired,
  deleteFrame: PropTypes.func.isRequired,
  addFrame: PropTypes.func.isRequired,
  pixelSize: PropTypes.number.isRequired,
};

export default FramesControl;
