import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowsAltH } from '@fortawesome/free-solid-svg-icons';
import ColorButton from './ColorButton';


function ColorPicker({
  onClick,
  mainColor,
  secondaryColor,
  swapColor,
}) {
  return (
    <div className="color-picker">
      <ColorButton
        buttonClass="main color-button"
        color={mainColor}
        id={0}
        value="#5e5e5e"
        onClick={onClick}
      />

      <ColorButton
        buttonClass="secondary color-button"
        color={secondaryColor}
        id={1}
        value="#7423a0"
        onClick={onClick}
      />

      <button
        onClick={swapColor}
        className="swap-color-button"
        type="button"
      >
        <FontAwesomeIcon icon={faArrowsAltH} />
      </button>
    </div>
  );
}

ColorPicker.propTypes = {
  onClick: PropTypes.func.isRequired,
  mainColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  secondaryColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  swapColor: PropTypes.func.isRequired,
};

export default ColorPicker;
