import React from 'react';
import PropTypes from 'prop-types';
import PreviewArea from './PreviewArea';
import PreviewSettings from './PreviewSettings';

class FramesPreview extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      fpsInputValue: 10,
    };

    this.previewArea = React.createRef();
    this.toggleFullScreen = this.toggleFullScreen.bind(this);
  }

  handleInput(value) {
    this.setState({ fpsInputValue: +value });
  }

  toggleFullScreen() {
    if (!document.fullscreenElement) {
      this.previewArea.current.requestFullscreen();
    } else {
      document.exitFullscreen();
    }
  }

  render() {
    const { layers, pixelSize } = this.props;
    const { fpsInputValue } = this.state;

    return (
      <div className="frames-preview" ref={this.previewArea}>
        <PreviewArea
          fpsInputValue={fpsInputValue}
          layers={layers}
          pixelSize={pixelSize}
        />

        <PreviewSettings
          value={fpsInputValue}
          onInput={value => this.handleInput(value)}
          onClick={this.toggleFullScreen}
        />
      </div>
    );
  }
}

FramesPreview.propTypes = {
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  pixelSize: PropTypes.number.isRequired,
};

export default FramesPreview;
