import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCompress } from '@fortawesome/free-solid-svg-icons';


class PreviewSettings extends React.Component {
  constructor(props) {
    super(props);

    this.getInpuValue = this.getInpuValue.bind(this);
    this.input = React.createRef();
  }

  getInpuValue() {
    const { onInput } = this.props;
    onInput(this.input.current.value);
  }

  render() {
    const { value, onClick } = this.props;
    return (
      <div className="preview-setings">
        <span className="fps-count">
          { value }
          {' '}
          FPS
        </span>

        <input
          type="range"
          className="fps-input"
          value={value}
          onChange={this.getInpuValue}
          min="0"
          max="24"
          ref={this.input}
        />

        <button
          onClick={onClick}
          className="full-screen-button"
          type="button"
        >
          <FontAwesomeIcon icon={faCompress} />
        </button>
      </div>
    );
  }
}

PreviewSettings.propTypes = {
  onInput: PropTypes.func.isRequired,
  onClick: PropTypes.func.isRequired,
  value: PropTypes.number.isRequired,
};

export default PreviewSettings;
