import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

function KeyboardSettingsButton({
  id,
  code,
  handleClick,
  icon,
  activeKeyButton,
}) {
  return (
    <div
      className="keyboard-settings-button"
    >
      <FontAwesomeIcon icon={icon} />

      <button
        className={`${activeKeyButton === id ? 'active ' : ''}key-button`}
        onClick={e => handleClick(id, e)}
        type="button"
      >
        {code}
      </button>

    </div>
  );
}

KeyboardSettingsButton.propTypes = {
  id: PropTypes.string.isRequired,
  code: PropTypes.string,
  handleClick: PropTypes.func.isRequired,

  icon: PropTypes.shape({
    iconName: PropTypes.string.isRequired,
    prefix: PropTypes.string.isRequired,
    icon: PropTypes.array.isRequired,
  }).isRequired,

  activeKeyButton: PropTypes.string.isRequired,
};

KeyboardSettingsButton.defaultProps = {
  code: '',
};


export default KeyboardSettingsButton;
