import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faAngleUp, faAngleDown } from '@fortawesome/free-solid-svg-icons';

function Layer({
  id,
  deleteLayer,
  changeCurrentLayer,
  currentLayer,
  layers,
  upLayer,
  downLayer,
}) {
  return (
    <div
      id={id}
      onClick={e => changeCurrentLayer(id, e)}
      className={`${currentLayer === id ? 'active ' : ''}layer-view`}
      role="presentation"
    >
      Layer
      {id + 1}

      <div
        className="layers-button-container"
      >
        {layers.length > 1 && id !== layers.length - 1 && (
          <button
            className="position layer-button"
            onClick={e => downLayer(id, e)}
            type="button"
          >
            <FontAwesomeIcon icon={faAngleDown} />
          </button>
        )}

        {layers.length > 1 && (
          <button
            className="delete layer-button"
            onClick={e => deleteLayer(id, e)}
            type="button"
          >
            <FontAwesomeIcon icon={faTrash} />
          </button>
        )}

        {layers.length > 1 && id !== 0 && (
          <button
            className="position layer-button"
            onClick={e => upLayer(id, e)}
            type="button"
          >
            <FontAwesomeIcon icon={faAngleUp} />
          </button>
        )}
      </div>
    </div>
  );
}

Layer.propTypes = {
  deleteLayer: PropTypes.func.isRequired,
  changeCurrentLayer: PropTypes.func.isRequired,
  upLayer: PropTypes.func.isRequired,
  downLayer: PropTypes.func.isRequired,
  currentLayer: PropTypes.number.isRequired,
  layers: PropTypes.arrayOf(PropTypes.array).isRequired,
  id: PropTypes.number.isRequired,
};

export default Layer;
