import React from 'react';
import PropTypes from 'prop-types';
import CopyButton from './CopyButton';
import DelteButton from './DeleteButton';
import renderCanvas from '../utils/renderCanvas';


class Frame extends React.Component {
  constructor(props) {
    super(props);

    this.canvas = React.createRef();
  }

  componentDidMount() {
    this.animateFrames();
  }

  componentDidUpdate() {
    this.animateFrames();
  }

  animateFrames() {
    const canvas = this.canvas.current;
    const { id, frames, pixelSize } = this.props;
    const frame = frames[id];
    renderCanvas(canvas, frame, pixelSize / 4);
  }

  render() {
    const {
      active,
      onClick,
      id,
      deleteFrame,
      copyFrame,
      frames,
    } = this.props;

    return (
      <div
        className={`${active ? 'active' : null} frame`}
        onClick={() => onClick(id)}
        role="presentation"
      >
        <canvas
          width="192"
          height="192"
          id={id}
          ref={this.canvas}
        />

        <div className="frame-count">{ id + 1 }</div>

        {frames.length > 1 && <DelteButton onClick={e => deleteFrame(id, e)} />}

        <CopyButton onClick={e => copyFrame(id, e)} />

      </div>
    );
  }
}

Frame.propTypes = {
  frames: PropTypes.arrayOf(PropTypes.array).isRequired,
  onClick: PropTypes.func.isRequired,
  copyFrame: PropTypes.func.isRequired,
  deleteFrame: PropTypes.func.isRequired,
  pixelSize: PropTypes.number.isRequired,
  id: PropTypes.number.isRequired,
  active: PropTypes.bool.isRequired,
};

export default Frame;
