import React from 'react';
import PropTypes from 'prop-types';

function CanvasSizeSettings({ changeCanvasSize }) {
  return (
    <div className="settings-area">
      <button
        className="canvas-size-button"
        onClick={() => changeCanvasSize(32)}
        type="button"
      >
        32x32
      </button>

      <button
        className="canvas-size-button"
        onClick={() => changeCanvasSize(64)}
        type="button"
      >
        64x64
      </button>

      <button
        className="canvas-size-button"
        onClick={() => changeCanvasSize(128)}
        type="button"
      >
        128x128
      </button>
    </div>
  );
}

CanvasSizeSettings.propTypes = {
  changeCanvasSize: PropTypes.func.isRequired,
};

export default CanvasSizeSettings;
