import React from 'react';
import PropTypes from 'prop-types';

function FrameInfo({
  mousePosX,
  mousePosY,
  canvasSize,
  framesLength,
  currentFrame,
  isNeedCoords,
}) {
  return (
    <div className="frame-info">
      <span>
        {currentFrame + 1}
        /
        {framesLength}
      </span>
      <span>
        [
        {canvasSize}
        x
        {canvasSize}
        ]
      </span>
      {isNeedCoords && (
        <span>
          {mousePosX}
          x
          {mousePosY}
        </span>
      )}
    </div>
  );
}

FrameInfo.propTypes = {
  mousePosX: PropTypes.number.isRequired,
  mousePosY: PropTypes.number.isRequired,
  canvasSize: PropTypes.number.isRequired,
  framesLength: PropTypes.number.isRequired,
  currentFrame: PropTypes.number.isRequired,
  isNeedCoords: PropTypes.bool.isRequired,
};

export default FrameInfo;
