import makeFrameCopy from './makeFrameCopy';

function erotateClockwis(frame) {
  const n = frame.length;
  const clone = makeFrameCopy(frame);

  for (let i = 0; i < n / 2; i += 1) {
    for (let j = i; j < n - i - 1; j += 1) {
      const tmp = clone[i][j];

      clone[i][j] = clone[n - j - 1][i];
      clone[n - j - 1][i] = clone[n - i - 1][n - j - 1];
      clone[n - i - 1][n - j - 1] = clone[j][n - i - 1];
      clone[j][n - i - 1] = tmp;
    }
  }

  return clone;
}


export default erotateClockwis;
