import makeFrameCopy from './makeFrameCopy';

function colorSamePixelsFunction(frame, color, pixelSize, e) {
  const clone = makeFrameCopy(frame);
  const startX = Math.floor(e.offsetX / pixelSize);
  const startY = Math.floor(e.offsetY / pixelSize);
  const startColor = clone[startX][startY] ? clone[startX][startY].join() : undefined;

  clone.forEach((row, x) => {
    row.forEach((cell, y) => {
      if (cell) {
        const correntColor = clone[x][y] ? clone[x][y].join() : undefined;
        if (correntColor === startColor) clone[x][y] = color;
      }
    });
  });

  return clone;
}

export default colorSamePixelsFunction;
