import makeFrameCopy from './makeFrameCopy';
import getCoordinates from './getCoordinates';

function createRectangle(frame, e, pixelSize, color, startX, startY) {
  const clone = makeFrameCopy(frame);
  const coordinates = getCoordinates(e, pixelSize);

  const deltaX = startX - coordinates.x;
  const deltaY = startY - coordinates.y;

  const minY = Math.min(startY, coordinates.y);
  const minX = Math.min(startX, coordinates.x);

  for (let i = minY; i <= minY + Math.abs(deltaY); i += 1) {
    clone[coordinates.x + deltaX][i] = color;
    clone[coordinates.x][i] = color;
  }

  for (let i = minX; i <= minX + Math.abs(deltaX); i += 1) {
    clone[i][coordinates.y] = color;
    clone[i][coordinates.y + deltaY] = color;
  }
  return clone;
}

export default createRectangle;
