import makeFrameCopy from './makeFrameCopy';

function negativeImage(frame) {
  const clone = makeFrameCopy(frame);

  clone.forEach((row, x) => {
    row.forEach((cell, y) => {
      if (cell) {
        const r = cell[0];
        const g = cell[1];
        const b = cell[2];
        clone[x][y] = [255 - r, 255 - g, 255 - b];
      }
    });
  });

  return clone;
}

export default negativeImage;
