function createLink(encoder, gifName) {
  const buffer = encoder.out.getData();
  const b64encoded = btoa(String.fromCharCode.apply(null, buffer));
  const datajpg = `data:image/jpg;base64,${b64encoded}`;
  const link = document.createElement('a');

  link.setAttribute('href', datajpg);
  link.setAttribute('download', `${gifName}.gif`);

  return link;
}

export default createLink;
