import React from 'react';
import PropTypes from 'prop-types';
import renderCanvas from '../utils/renderCanvas';
import makeFrameCopy from '../utils/makeFrameCopy';
import createRectangle from '../utils/createRectangle';
import createCircle from '../utils/createCircle';
import bucketFunction from '../utils/bucketFunction';
import lineDraw from '../utils/lineDraw';
import lighten from '../utils/lighten';
import getCoordinates from '../utils/getCoordinates';
import colorSamePixelsFunction from '../utils/colorSamePixelsFunction';
import erotateClockwis from '../utils/erotateClockwis';
import moveFunction from '../utils/moveFunction';
import dithering from '../utils/dithering';
import negativeImage from '../utils/negativeImage';
import broom from '../utils/broom';

class MainCanvas extends React.PureComponent {
  constructor(props) {
    super(props);

    this.penOrEraserOrMirror = this.penOrEraserOrMirror.bind(this);
    this.canvas = React.createRef();
    this.chooseToolFunction = this.chooseToolFunction.bind(this);
    this.rectangleAndCircle = this.rectangleAndCircle.bind(this);
    this.stroke = this.stroke.bind(this);
    this.lighten = this.lighten.bind(this);
    this.rotateFrame = this.rotateFrame.bind(this);
    this.moveFrame = this.moveFrame.bind(this);
    this.ditheringFrame = this.ditheringFrame.bind(this);
    this.bucketOrRandomBucket = this.bucketOrRandomBucket.bind(this);
  }

  componentDidUpdate() {
    const { frame, pixelSize } = this.props;
    const canvas = this.canvas.current;
    renderCanvas(canvas, frame, pixelSize);
  }

  chooseToolFunction(e) {
    const { activeToolId } = this.props;

    switch (activeToolId) {
      case 'pen':
      case 'eraser':
      case 'mirror':
        this.penOrEraserOrMirror(e);
        break;
      case 'paintBucket':
      case 'randomBucket':
        this.bucketOrRandomBucket(e);
        break;
      case 'circle':
      case 'rectangle':
        this.rectangleAndCircle(activeToolId, e);
        break;
      case 'stroke':
        this.stroke(e);
        break;
      case 'paintAllPixels':
        this.colorSamePixels(e);
        break;
      case 'lighten':
        this.lighten(e);
        break;
      case 'rotate90deg':
        this.rotateFrame();
        break;
      case 'move':
        this.moveFrame(e);
        break;
      case 'dithering':
        this.ditheringFrame(e);
        break;
      case 'colorPicker':
        this.colorPicker(e);
        break;
      case 'shapeSlection':
        this.shapeSlection(e);
        break;
      case 'negative':
      case 'broom':
        this.negativeOrBroom();
        break;
      default:
        break;
    }
  }

  negativeOrBroom() {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const {
      frame,
      changeFrame,
      activeToolId,
    } = this.props;
    const chozenFunction = activeToolId === 'negative' ? negativeImage : broom;
    const newFrame = chozenFunction(frame);
    changeFrame(newFrame);
  }

  colorPicker(e) {
    const { frame, changeColor, pixelSize } = this.props;
    const coordinates = getCoordinates(e, pixelSize);
    const color = frame[coordinates.x][coordinates.y];
    if (!color) return;
    const targetButton = e.nativeEvent.which;
    changeColor(color, targetButton);
  }

  ditheringFrame(event) {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
    } = this.props;

    const startCoords = getCoordinates(event, pixelSize);
    const color = event.nativeEvent.which === 3 ? secondaryColor : mainColor;
    let clone = dithering(frame, color, startCoords.x, startCoords.y);

    renderCanvas(canvas, clone, pixelSize);

    canvas.onmousemove = (e) => {
      const currentCoords = getCoordinates(e, pixelSize);
      clone = dithering(clone, color, currentCoords.x, currentCoords.y);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }

  moveFrame(event) {
    const { frame, pixelSize, changeFrame } = this.props;
    const canvas = this.canvas.current;
    const startCoords = getCoordinates(event, pixelSize);

    let clone = makeFrameCopy(frame);
    canvas.onmouseup = null;

    canvas.onmousemove = (e) => {
      const startFrame = makeFrameCopy(frame);
      const currCoords = getCoordinates(e, pixelSize);
      clone = moveFunction(startFrame, startCoords.x, startCoords.y, currCoords.x, currCoords.y);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }

  rotateFrame() {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const { frame, changeFrame } = this.props;
    const clone = erotateClockwis(frame);

    changeFrame(clone);
  }

  colorSamePixels(e) {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
    } = this.props;

    const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
    const clone = colorSamePixelsFunction(frame, colorRGBA, pixelSize, e.nativeEvent);
    changeFrame(clone);
  }

  stroke(event) {
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
    } = this.props;

    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const color = event.nativeEvent.which === 3 ? secondaryColor : mainColor;
    const startCoords = getCoordinates(event, pixelSize);
    let clone = makeFrameCopy(frame);

    lineDraw(clone, color, startCoords.x, startCoords.y, startCoords.x, startCoords.y);
    renderCanvas(canvas, frame, pixelSize);

    canvas.onmousemove = (e) => {
      const startFrame = makeFrameCopy(frame);
      const currCoords = getCoordinates(e, pixelSize);

      clone = lineDraw(startFrame, color, startCoords.x, startCoords.y, currCoords.x, currCoords.y);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }


  rectangleAndCircle(activeToolId, event) {
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
    } = this.props;

    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const color = event.nativeEvent.which === 3 ? secondaryColor : mainColor;
    const startCoords = getCoordinates(event, pixelSize);
    const chozenFunction = activeToolId === 'rectangle' ? createRectangle : createCircle;
    let clone = makeFrameCopy(frame);

    clone = lineDraw(clone, color, startCoords.x, startCoords.y, startCoords.x, startCoords.y);
    renderCanvas(canvas, clone, pixelSize);

    canvas.onmousemove = (e) => {
      clone = chozenFunction(frame, e, pixelSize, color, startCoords.x, startCoords.y);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }

  bucketOrRandomBucket(e) {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
      activeToolId,
    } = this.props;

    const colorRGBA = e.nativeEvent.which === 3 ? secondaryColor : mainColor;
    const isRandomColor = activeToolId === 'randomBucket';
    const clone = bucketFunction(frame, colorRGBA, pixelSize, e.nativeEvent, isRandomColor);

    changeFrame(clone);
  }

  penOrEraserOrMirror(event) {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const {
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      changeFrame,
      activeToolId,
    } = this.props;

    const coords = getCoordinates(event, pixelSize);
    const isMirror = activeToolId === 'mirror';
    const coordinates = [coords.x, coords.y];
    let color = event.nativeEvent.which === 3 ? secondaryColor : mainColor;
    if (activeToolId === 'eraser') { color = undefined; }
    let clone = lineDraw(frame, color, coords.x, coords.y, coords.x, coords.y, isMirror);

    renderCanvas(canvas, clone, pixelSize);

    canvas.onmousemove = (e) => {
      const currentCoords = getCoordinates(e, pixelSize);

      coordinates.push(currentCoords.x, currentCoords.y);
      if (coordinates.length > 4) coordinates.splice(0, 2);

      clone = lineDraw(clone, color, ...coordinates, isMirror);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }

  lighten(event) {
    const canvas = this.canvas.current;
    canvas.onmouseup = null;
    const { frame, pixelSize, changeFrame } = this.props;
    const startCoords = getCoordinates(event, pixelSize);
    let color = lighten(frame[startCoords.x][startCoords.y]);

    let clone = lineDraw(frame, color, startCoords.x, startCoords.y, startCoords.x, startCoords.y);
    renderCanvas(canvas, clone, pixelSize);

    canvas.onmousemove = (e) => {
      const currCoords = getCoordinates(e, pixelSize);
      color = lighten(clone[currCoords.x][currCoords.y]);

      clone = lineDraw(clone, color, currCoords.x, currCoords.y, currCoords.x, currCoords.y);
      renderCanvas(canvas, clone, pixelSize);
    };

    canvas.onmouseup = () => {
      canvas.onmousemove = null;
      changeFrame(clone);
    };
  }

  render() {
    return (
      <canvas
        onMouseDown={this.chooseToolFunction}
        onContextMenu={e => e.preventDefault()}
        className="main-canvas"
        width="768"
        height="768"
        ref={this.canvas}
      />
    );
  }
}

MainCanvas.propTypes = {
  secondaryColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  mainColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  frame: PropTypes.arrayOf(PropTypes.array).isRequired,
  changeFrame: PropTypes.func.isRequired,
  pixelSize: PropTypes.number.isRequired,
  changeColor: PropTypes.func.isRequired,
  activeToolId: PropTypes.string.isRequired,
};

export default MainCanvas;
