import React from 'react';
import PropTypes from 'prop-types';
import FrameInfo from './FrameInfo';
import MainCanvas from './Canvas';

class CanvasContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      x: 0,
      y: 0,
      isNeedCoords: false,
    };

    this.changeMousePosition = this.changeMousePosition.bind(this);
    this.deleteCoords = this.deleteCoords.bind(this);
    this.canvasContainer = React.createRef();
  }

  deleteCoords() {
    this.setState({
      isNeedCoords: false,
    });
  }

  changeMousePosition(e) {
    const { pixelSize } = this.props;
    const x = Math.floor(e.nativeEvent.offsetX / pixelSize);
    const y = Math.floor(e.nativeEvent.offsetY / pixelSize);

    this.setState({
      x,
      y,
      isNeedCoords: true,
    });
  }

  render() {
    const {
      activeToolId,
      changeFrame,
      frame,
      mainColor,
      secondaryColor,
      pixelSize,
      canvasSize,
      currentFrame,
      framesLength,
      changeColor,
    } = this.props;

    const { x, y, isNeedCoords } = this.state;

    return (
      <div className="main-canvas">
        <div
          className="main-canvas-container"
          onMouseMove={this.changeMousePosition}
          onMouseLeave={this.deleteCoords}
          ref={this.canvasContainer}
        >
          <MainCanvas
            activeToolId={activeToolId}
            changeFrame={changeFrame}
            frame={frame}
            mainColor={mainColor}
            secondaryColor={secondaryColor}
            pixelSize={pixelSize}
            canvasSize={canvasSize}
            currentFrame={currentFrame}
            framesLength={framesLength}
            changeColor={changeColor}
          />
        </div>

        <div className="main-canvas-info">
          <FrameInfo
            canvasSize={canvasSize}
            framesLength={framesLength}
            currentFrame={currentFrame}
            mousePosX={x}
            mousePosY={y}
            isNeedCoords={isNeedCoords}
          />
        </div>
      </div>
    );
  }
}

CanvasContainer.propTypes = {
  secondaryColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  mainColor: PropTypes.arrayOf(PropTypes.number).isRequired,
  frame: PropTypes.arrayOf(PropTypes.array).isRequired,
  canvasSize: PropTypes.number.isRequired,
  changeFrame: PropTypes.func.isRequired,
  pixelSize: PropTypes.number.isRequired,
  currentFrame: PropTypes.number.isRequired,
  framesLength: PropTypes.number.isRequired,
  changeColor: PropTypes.func.isRequired,
  activeToolId: PropTypes.string.isRequired,
};

export default CanvasContainer;
