import React from 'react';
import PropTypes from 'prop-types';
import { OAuth, Provider } from '@zalando/oauth2-client-js';

class Header extends React.Component {
  provider() {
    const google = new OAuth.Provider({
      id: ' google ',
      authorization_url: ' https://google.com/auth '
    });
  };

  render() {
    const { isLandingPage, disActiveLandingPage, activeLandingPage } = this.props;
    return (
      <div className="app-header">

        <button
          className="piskel-button"
          onClick={activeLandingPage}
          type="button"
        >
          <h1>Piskel</h1>
        </button>


        <div className="header-buttons-container">
          {isLandingPage && (
            <button
              className="create-sprite-button"
              onClick={disActiveLandingPage}
              type="button"
            >
              Create sprite
            </button>
          )
          }

          <button
            className="create-sprite-button"
            type="button"
          >
            Sing in
          </button>
        </div>
      </div>
    );
  }
}

Header.propTypes = {
  isLandingPage: PropTypes.bool.isRequired,
  disActiveLandingPage: PropTypes.func.isRequired,
  activeLandingPage: PropTypes.func.isRequired,
};


export default Header;
