import makeFrameCopy from './makeFrameCopy';

function moveFunction(frame, startX, startY, x, y) {
  const deltaX = x - startX;
  const deltaY = startY - y;
  let clone = makeFrameCopy(frame);
  const canvasSize = clone.length;

  if (deltaX > 0) {
    clone = Array.from(Array(deltaX), () => new Array(canvasSize)
      .fill(0)).concat(clone.slice(0, -deltaX));
  }

  if (deltaX < 0) {
    clone = clone.slice(-deltaX)
      .concat(Array.from(Array(-deltaX), () => new Array(canvasSize).fill(0)));
  }

  for (let i = 0; i < canvasSize; i += 1) {
    if (deltaY < 0) {
      clone[i] = Array(-deltaY).fill(undefined).concat(clone[i].slice(0, deltaY));
    }

    if (deltaY > 0) {
      clone[i] = clone[i].slice(deltaY).concat((Array(deltaY).fill(undefined)));
    }
  }

  return clone;
}

export default moveFunction;
