import React from 'react';
import PropTypes from 'prop-types';
import {
  faPen,
  faFillDrip,
  faEraser,
  faThumbtack,
  faRadiation,
  faSun,
  faGripLinesVertical,
  faUndoAlt,
  faChessBoard,
  faEyeDropper,
  faDice,
  faCameraRetro,
  faBroom,
} from '@fortawesome/free-solid-svg-icons';
import { faCircle, faSquare, faHandPaper } from '@fortawesome/free-regular-svg-icons';
import key from 'weak-key';
import constants from '../constants';
import ToolButton from './ToolButton';

function ToolsControl({ onClick, activeToolId }) {
  const Icons = [
    faPen,
    faEraser,
    faFillDrip,
    faCircle,
    faSquare,
    faThumbtack,
    faRadiation,
    faSun,
    faGripLinesVertical,
    faUndoAlt,
    faHandPaper,
    faChessBoard,
    faEyeDropper,
    faDice,
    faCameraRetro,
    faBroom,
  ];

  return (
    <div className="tools-controle">
      {
        Icons.map((item, i) => (
          <ToolButton
            key={key(item)}
            icon={item}
            onClick={onClick}
            id={constants.toolButtonsNames[i]}
            activeToolId={activeToolId}
          />
        ))
      }
    </div>
  );
}

ToolsControl.propTypes = {
  onClick: PropTypes.func.isRequired,
  activeToolId: PropTypes.string.isRequired,
};

export default ToolsControl;
