import React from 'react';
import Settings from './Settings/Settings';
import FramesPreview from './FramesPreview/FramesPreview';
import FramesControl from './FramesControl/FramesControl';
import ToolsControl from './Tools/ToolsControl';
import CanvasContainer from './Canvas/CanvasContainer';
import LayerSettings from './LayerSettings/LayerSettings';
import CreateBaseFrame from './utils/CreateBaseFrame';
import ColorPicker from './Tools/ColorPicker';
import hexToRgb from './utils/hexToRGB';
import makeFrameCopy from './utils/makeFrameCopy';
import KeyboardSettings from './Tools/KeyboardSettings';

class Main extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activeToolId: 'pen',
      pixelSize: 24,
      canvasSize: 32,
      layers: [[CreateBaseFrame(32)]],
      currentFrame: 0,
      currentLayer: 0,
      mainColor: [0, 0, 0],
      secondaryColor: [116, 35, 160],
      buttonsConfiguration: ['P', 'E', 'B', 'C', 'R', 'L', 'A', 'I', 'M', 'T', 'M', 'D', 'O', 'X', 'N', 'W'],
    };

    this.changeToolId = this.changeToolId.bind(this);
    this.handleFrameClick = this.handleFrameClick.bind(this);
    this.changeFrame = this.changeFrame.bind(this);
    this.addFrame = this.addFrame.bind(this);
    this.deleteFrame = this.deleteFrame.bind(this);
    this.copyFrame = this.copyFrame.bind(this);
    this.handleColorPickerClick = this.handleColorPickerClick.bind(this);
    this.swapColor = this.swapColor.bind(this);
    this.changeCanvasSize = this.changeCanvasSize.bind(this);
    this.addLayer = this.addLayer.bind(this);
    this.deleteLayer = this.deleteLayer.bind(this);
    this.changeCurrentLayer = this.changeCurrentLayer.bind(this);
    this.upLayer = this.upLayer.bind(this);
    this.downLayer = this.downLayer.bind(this);
    this.changeColor = this.changeColor.bind(this);
    this.changeButtonConfig = this.changeButtonConfig.bind(this);
  }

  componentDidMount() {
    const newState = JSON.parse(localStorage.getItem('state'));
    if (newState) {
      this.setState({
        activeToolId: newState.activeToolId,
        pixelSize: newState.pixelSize,
        canvasSize: newState.canvasSize,
        layers: newState.layers,
        currentFrame: newState.currentFrame,
        currentLayer: newState.currentLayer,
        mainColor: newState.mainColor,
        secondaryColor: newState.secondaryColor,
        buttonsConfiguration: newState.buttonsConfiguration,
      });
    }

    window.onunload = () => {
      const jsonState = JSON.stringify(this.state);
      localStorage.setItem('state', jsonState);
    };
  }

  componentWillUnmount() {
    const jsonState = JSON.stringify(this.state);
    localStorage.setItem('state', jsonState);
  }

  changeFrame(frame) {
    const { layers, currentFrame, currentLayer } = this.state;
    layers[currentLayer][currentFrame] = frame;
    this.setState({ layers });
  }

  changeToolId(id) {
    this.setState({ activeToolId: id });
  }

  handleColorPickerClick(id, color) {
    if (id === 0) this.setState({ mainColor: hexToRgb(color) });
    if (id === 1) this.setState({ secondaryColor: hexToRgb(color) });
  }

  swapColor() {
    const { secondaryColor, mainColor } = this.state;

    this.setState({
      mainColor: secondaryColor,
      secondaryColor: mainColor,
    });
  }

  changeColor(color, targetButton) {
    if (targetButton === 1) {
      this.setState({
        mainColor: color,
      });
    }

    if (targetButton === 3) {
      this.setState({
        secondaryColor: color,
      });
    }
  }

  handleFrameClick(id) {
    this.setState({ currentFrame: id });
  }

  addFrame() {
    const { layers, canvasSize } = this.state;

    layers.forEach(frames => frames.push(CreateBaseFrame(canvasSize)));
    this.setState({ layers });
  }

  copyFrame(id, e) {
    e.stopPropagation();

    const { layers } = this.state;

    layers.forEach((frames) => {
      const clone = makeFrameCopy(frames[id]);
      frames.splice(id, 0, clone);
    });
    this.setState({ layers });
  }

  deleteFrame(id, e) {
    e.stopPropagation();
    const { layers, currentLayer } = this.state;

    layers.forEach((frames) => {
      frames.splice(id, 1);
    });

    this.setState({
      currentFrame: layers[currentLayer].length === id ? id - 1 : id,
      layers,
    });
  }

  changeCanvasSize(newCanvasSize) {
    const {
      canvasSize, layers, currentLayer, pixelSize,
    } = this.state;
    if (newCanvasSize === canvasSize) return;
    const delta = Math.abs(newCanvasSize - canvasSize) / 2;
    const pixelDelta = canvasSize / newCanvasSize;
    const framesLength = layers[currentLayer].length;
    const mainCanvasContainer = document.querySelector('.main-canvas-container');

    mainCanvasContainer.style.backgroundSize = `${pixelSize * pixelDelta}px ${pixelSize * pixelDelta}px`;

    for (let j = 0; j < layers.length; j += 1) {
      if (newCanvasSize > canvasSize) {
        for (let i = 0; i < framesLength; i += 1) {
          const frame = layers[j][i];

          for (let x = 0; x < canvasSize; x += 1) {
            frame[x] = Array(delta).concat(frame[x]);
            frame[x] = frame[x].concat(Array(delta));
          }

          for (let x = 0; x < delta; x += 1) {
            frame.unshift(Array(delta * 2));
            frame.push(Array(delta * 2));
          }
        }
      }

      if (newCanvasSize < canvasSize) {
        for (let i = 0; i < framesLength; i += 1) {
          const frame = layers[j][i];
          frame.splice(0, delta);
          frame.splice(newCanvasSize, delta);

          for (let x = 0; x < newCanvasSize; x += 1) {
            frame[x].splice(0, delta);
            frame[x].splice(newCanvasSize, delta);
          }
        }
      }
    }

    this.setState({
      pixelSize: pixelSize * pixelDelta,
      canvasSize: newCanvasSize,
      layers,
    });
  }

  addLayer() {
    const { layers, canvasSize } = this.state;
    const framesCount = layers[0].length;
    const newLayer = [];

    for (let i = 0; i < framesCount; i += 1) {
      newLayer.push(CreateBaseFrame(canvasSize));
    }

    layers.push(newLayer);
    this.setState({ layers });
  }

  deleteLayer(id, e) {
    e.stopPropagation();
    const { layers } = this.state;
    layers.splice(id, 1);
    this.setState({
      currentLayer: layers.length === id ? id - 1 : id,
      layers,

    });
  }

  changeCurrentLayer(id, e) {
    e.stopPropagation();

    this.setState({
      currentLayer: id,
      currentFrame: 0,
    });
  }

  upLayer(layerId, e) {
    const { layers } = this.state;
    const upedLayer = layers.splice(layerId, 1);
    layers.splice(layerId - 1, 0, ...upedLayer);

    this.setState({
      layers,
      currentLayer: layerId - 1,
    });

    e.stopPropagation();
  }

  downLayer(layerId, e) {
    const { layers } = this.state;
    const downedLayer = layers.splice(layerId + 1, 1);

    layers.splice(layerId, 0, ...downedLayer);

    this.setState({
      layers,
      currentLayer: layerId + 1,
    });

    e.stopPropagation();
  }

  changeButtonConfig(newButtonConfig) {
    this.setState({
      buttonsConfiguration: newButtonConfig,
    });
  }

  render() {
    const {
      activeToolId,
      pixelSize,
      layers,
      currentFrame,
      mainColor,
      secondaryColor,
      canvasSize,
      currentLayer,
      buttonsConfiguration,
    } = this.state;

    return (
      <div
        className="app-main"
      >
        <div className="tools">
          <ToolsControl
            onClick={this.changeToolId}
            activeToolId={activeToolId}
          />

          <ColorPicker
            onClick={this.handleColorPickerClick}
            mainColor={mainColor}
            secondaryColor={secondaryColor}
            swapColor={this.swapColor}
          />

          <KeyboardSettings
            buttonsConfiguration={buttonsConfiguration}
            changeButtonConfig={this.changeButtonConfig}
            changeToolId={this.changeToolId}
          />
        </div>

        <FramesControl
          frames={layers[currentLayer]}
          pixelSize={pixelSize}
          onClick={this.handleFrameClick}
          addFrame={this.addFrame}
          currentFrame={currentFrame}
          copyFrame={this.copyFrame}
          deleteFrame={this.deleteFrame}
        />

        <CanvasContainer
          activeToolId={activeToolId}
          changeFrame={this.changeFrame}
          frame={makeFrameCopy(layers[currentLayer][currentFrame])}
          mainColor={mainColor}
          secondaryColor={secondaryColor}
          pixelSize={pixelSize}
          changeMousePosition={this.changeMousePosition}
          canvasSize={canvasSize}
          currentFrame={currentFrame}
          framesLength={layers[currentLayer].length}
          changeColor={this.changeColor}
        />

        <div className="preview-tools">
          <FramesPreview
            layers={layers}
            pixelSize={pixelSize}
          />

          <LayerSettings
            currentLayer={currentLayer}
            layers={layers}
            addLayer={this.addLayer}
            deleteLayer={this.deleteLayer}
            changeCurrentLayer={this.changeCurrentLayer}
            downLayer={this.downLayer}
            upLayer={this.upLayer}
          />
        </div>

        <Settings
          changeCanvasSize={this.changeCanvasSize}
          layers={layers}
          pixelSize={pixelSize}
        />
      </div>
    );
  }
}

export default Main;
