import getRandomColor from './getRandomColor';
import makeFrameCopy from './makeFrameCopy';

function bucketFunction(frame, color, pixelSize, e, isRandomColor) {
  const clone = makeFrameCopy(frame);
  const startX = Math.floor(e.offsetX / pixelSize);
  const startY = Math.floor(e.offsetY / pixelSize);
  const canvasSize = clone.length;
  const startColor = clone[startX][startY] ? clone[startX][startY].join() : undefined;

  if (startColor !== color.join()) {
    const stack = [[startX, startY]];

    while (stack.length) {
      const position = stack.pop();
      const x = position[0];
      const y = position[1];

      if ((x >= 0 && x < canvasSize) && (y >= 0 && y < canvasSize)) {
        const correntColor = clone[x][y] ? clone[x][y].join() : undefined;
        if (startColor === correntColor) {
          clone[x][y] = isRandomColor ? getRandomColor() : color;
          stack.push([x - 1, y], [x + 1, y], [x, y - 1], [x, y + 1]);
        }
      }
    }
  }

  return clone;
}

export default bucketFunction;
