import React from 'react';
import Header from './Header';
import Main from './Main';
import LandingPage from './LandingPage';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLandingPage: true,
    };

    this.activeLandingPage = this.activeLandingPage.bind(this);
    this.disActiveLandingPage = this.disActiveLandingPage.bind(this);
  }

  activeLandingPage() {
    this.setState({
      isLandingPage: true,
    });
  }

  disActiveLandingPage() {
    this.setState({
      isLandingPage: false,
    });
  }

  render() {
    const { isLandingPage } = this.state;

    return (
      <div className="App">
        <Header
          disActiveLandingPage={this.disActiveLandingPage}
          activeLandingPage={this.activeLandingPage}
          isLandingPage={isLandingPage}
        />

        {isLandingPage
          && (
            <LandingPage
              disActiveLandingPage={this.disActiveLandingPage}
            />
          )
        }

        {!isLandingPage && <Main />}
      </div>
    );
  }
}

export default App;
